.reviews{
    max-width: 800px;
    margin: 0 auto;
    margin-bottom: 100px;
}

.reviews__title{
    margin-bottom: 40px;
}

.reviews__text{
    font-size: 24px;
    line-height: 40px;
    font-weight: 300;
}

.reviews__name{
    display: block;
    font-family: $font-second;
    font-weight: 700;
    font-size: 18px;
}

.reviews__caption{
    display: block;
    color: $font-grey;
}
