.testimonials{
    max-width: 1200px;
    display: flex;
    margin: 0 50px;
    margin-bottom: 100px;
    gap: 60px;
}

.testimonials__list-wrap{
    max-width: 600px;
    padding: 30px;
    background-color: $section-back;
}

.testimonials__list-item{
    @include reset-list;
    margin-bottom: 20px;
    padding-left: 50px;
    background-image: url(../../img/icons/check-circle.svg);
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 30px 30px;
    font-family: $font-basic;
    font-weight: 300;
    font-size: 16px;
    text-align: left;
}

.testimonials__quote{
    display: flex;
    flex-direction: column;
    max-width: 600px;
    font-family: $font-basic;
    font-weight: 300;
    text-align: left;
}

.testimonials__title{
    margin: 0;
    margin-bottom: 30px;
    padding: 0;
    font-family: $font-basic;
    font-weight: 300;
    font-size: 32px;
}

.testimonials__text{
    color: $font-grey;
    font-size: 20px;
}

.testimonials__sign{
    padding-top: 100px;
    background-image: url(../../img/sign.svg);
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 200px 120px;
}

.testimonials__name{
    display: block;
    font-family: $font-basic;
    font-weight: 600;
    font-size: 16px;
}

.testimonials__caption{
    display: block;
    color: $font-grey;
}
