.appointment{
    max-width: 1200px;
    margin: 0 auto;
    margin-bottom: 100px;
    background-color: $section-back;
    padding: 60px;
}

.appointment__input{
    width: 100%;
    border: none;
    border-bottom: 1px solid $font-color;
    background-color: $section-back;
}

.appointment__form{
    max-width: 700px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.appointment__textarea{
    resize: none;
}

.appointment__btn{
    width: 100%;
    border-radius: 0;
    font-size: 19px;
    text-transform: capitalize;
}
