@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin font-text {
  font-family: $font-basic;
  font-size: 40px;
font-style: normal;
font-weight: 400;
line-height: 60px;
}

@mixin font-title {
  font-family: $font-basic;
  font-size: 52px;
font-style: normal;
font-weight: 300;
line-height: 62.4px;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body{
  background-color: $basic-back;
  color: $font-color;
  width: 100%;
  padding: 0;
  margin: 0;
  font-family: $font-basic;
  text-align: center;
}

.button{
  width: 593px;
  height: 103px;
  padding: 30px 51px;
  background-color: $btn-back;
  border: 2px solid $btn-back;
  border-radius: 10px;
  font-family: $font-basic;
  color: $basic-back;
  font-weight: 700;
  font-size: 24px;
  text-transform: uppercase;
  cursor: pointer;
}

.title{
  padding: 0;
  font-family: $font-basic;
  font-weight: 300;
  font-size: 32px;
}
