@font-face {
  font-family: "Inter";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Inter";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Inter-Bold.woff2") format("woff2");
}
@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Roboto-Bold.woff2") format("woff2");
}
@font-face {
  font-family: "Roboto Serif";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoSerif-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Roboto Serif";
  font-weight: 100;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoSerif-Thin.woff2") format("woff2");
}
@font-face {
  font-family: "Roboto Serif";
  font-weight: 200;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoSerif-ExtraLight.woff2") format("woff2");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body {
  background-color: #FFFFFF;
  color: #111111;
  width: 100%;
  padding: 0;
  margin: 0;
  font-family: "Roboto Serif", serif;
  text-align: center;
}

.button {
  width: 593px;
  height: 103px;
  padding: 30px 51px;
  background-color: #FFC43F;
  border: 2px solid #FFC43F;
  border-radius: 10px;
  font-family: "Roboto Serif", serif;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 24px;
  text-transform: uppercase;
  cursor: pointer;
}

.title {
  padding: 0;
  font-family: "Roboto Serif", serif;
  font-weight: 300;
  font-size: 32px;
}

.header__wrapper {
  max-width: 1200px;
  margin: 0 auto 30px;
  padding: 50px 110px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__nav-list {
  display: flex;
  gap: 50px;
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}

.header__title {
  margin: 0;
  font-family: "Roboto Serif", serif;
  font-size: 48px;
  font-weight: 200;
  text-transform: uppercase;
}

.page-main__wrap {
  max-width: 1200px;
  margin: 0 auto;
}

.promo {
  max-width: 1200px;
  margin: 0 auto 100px;
  display: flex;
  align-items: center;
}

.promo__content {
  width: 600px;
  margin-right: -90px;
  padding: 50px;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  background-color: #FFFFFF;
}

.promo__text-basic {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 26px;
  line-height: 40px;
}

.promo__text-add {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #6D6D6D;
}

.session {
  max-width: 1200px;
  padding: 150px 0;
  margin: 0 auto;
  margin-bottom: 100px;
  background-image: url(../../img/sectionback.png);
}

.session__content {
  margin: 0 auto;
  padding: 40px;
  max-width: 700px;
  background-color: #FFFFFF;
}

.session__text {
  margin-bottom: 30px;
  font-family: "Roboto Serif", serif;
  font-size: 28px;
  line-height: 40px;
  font-weight: 300;
}

.testimonials {
  max-width: 1200px;
  display: flex;
  margin: 0 50px;
  margin-bottom: 100px;
  gap: 60px;
}

.testimonials__list-wrap {
  max-width: 600px;
  padding: 30px;
  background-color: #FFF6F1;
}

.testimonials__list-item {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-bottom: 20px;
  padding-left: 50px;
  background-image: url(../../img/icons/check-circle.svg);
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 30px 30px;
  font-family: "Roboto Serif", serif;
  font-weight: 300;
  font-size: 16px;
  text-align: left;
}

.testimonials__quote {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  font-family: "Roboto Serif", serif;
  font-weight: 300;
  text-align: left;
}

.testimonials__title {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  font-family: "Roboto Serif", serif;
  font-weight: 300;
  font-size: 32px;
}

.testimonials__text {
  color: #6D6D6D;
  font-size: 20px;
}

.testimonials__sign {
  padding-top: 100px;
  background-image: url(../../img/sign.svg);
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 200px 120px;
}

.testimonials__name {
  display: block;
  font-family: "Roboto Serif", serif;
  font-weight: 600;
  font-size: 16px;
}

.testimonials__caption {
  display: block;
  color: #6D6D6D;
}

.add-block {
  max-width: 1200px;
  display: flex;
  gap: 40px;
  margin-bottom: 100px;
}

.add-block__text {
  padding: 90px 80px;
  background-color: #F5F6F6;
  font-size: 24px;
  line-height: 40px;
  font-weight: 300;
}

.add-block__second {
  padding: 60px 50px;
}

.reviews {
  max-width: 800px;
  margin: 0 auto;
  margin-bottom: 100px;
}

.reviews__title {
  margin-bottom: 40px;
}

.reviews__text {
  font-size: 24px;
  line-height: 40px;
  font-weight: 300;
}

.reviews__name {
  display: block;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 18px;
}

.reviews__caption {
  display: block;
  color: #6D6D6D;
}

.appointment {
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 100px;
  background-color: #FFF6F1;
  padding: 60px;
}

.appointment__input {
  width: 100%;
  border: none;
  border-bottom: 1px solid #111111;
  background-color: #FFF6F1;
}

.appointment__form {
  max-width: 700px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.appointment__textarea {
  resize: none;
}

.appointment__btn {
  width: 100%;
  border-radius: 0;
  font-size: 19px;
  text-transform: capitalize;
}

.contacts {
  margin-bottom: 100px;
}

.contacts__title {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  text-transform: uppercase;
  font-weight: 200;
  font-size: 60px;
}

.contacts__list {
  max-width: 300px;
  margin: 0;
  padding: 0;
  list-style: none;
  margin: 0 auto;
  display: flex;
  gap: 15px;
}