.promo{
    max-width: 1200px;
    margin: 0 auto 100px;
    display: flex;
    align-items: center;
}

.promo__content{
    width: 600px;
    margin-right: -90px;
    padding: 50px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    background-color: $basic-back;
}

.promo__text-basic{
    font-family: $font-second;
    font-weight: 400;
    font-size: 26px;
    line-height: 40px;
}

.promo__text-add{
    font-family: $font-second;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: $font-grey;
}
