.header__wrapper{
    max-width: 1200px;
    margin: 0 auto 30px;
    padding: 50px 110px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__nav-list{
    display: flex;
    gap: 50px;
    @include reset-list;
    font-family: $font-menu;
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
}

.header__title{
    margin: 0;
    font-family: $font-basic;
    font-size: 48px;
    font-weight: 200;
    text-transform: uppercase;
}
